<template>
  <div class="health-info">
    <div class="health-con" >
      <div class="con">
        <p class="tips">本人承诺被保险人<span>{{this.$route.query.name}}</span>未正在以及未曾经患有以下疾病、症状或存在下列行为：</p>
        <p class="info" v-html="healthInfo"></p>

      </div>
      <div class="helth-btns">
        <p class="btn btn-cancel" @click="onCancel">有部分以上疾病</p>
        <p class="btn btn-sure" @click="onSure" >确认无以上疾病</p>
      </div>
    </div>
    <occupy-alert :title="alertTitle" v-if="healthInfoShow" @handleBtnRightEvent="healthInfoShow=false"></occupy-alert>
  </div>
</template>

<script>
  import occupyAlert from '@/components/product/TheOccupyAlert'
  import {  getLocalStorage } from '@/assets/js/localStorage'
export default {
  name: 'TheHealthInfo',
  components:{
    occupyAlert
  },
  data () {
    return {
      healthInfoShow:false,
      healthInfo: '',
      alertTitle:''
    }
  },
  mounted () {
    this.getHealthInfo()
    this.postInsured = getLocalStorage('postInsured')
  },
  methods: {
    onCancel () {
      this.healthInfoShow=true
      this.alertTitle = `非常抱歉！<br/>
被保险人未能通过本次投保审核！`
    },
    onSure () {
      this.$api.product.orderSubnit(this.postInsured).then(res => {
        if (res.code === 1) {
          this.$router.push({
            path: '/product/orderConfirm',
            query: {
              sn: res.data,
              productId: this.$route.query.productId
            }
          })
        } else {
          this.$toast.center(res.message)
        }
      })
    },
    getHealthInfo () {
      let postData = {
        id: this.$route.query.productId
      }
      this.$api.product.healthInfo(postData).then(res => {
        if (res.code === 1) {
          this.healthInfo = res.data
        }
      })
    }

  },
  computed: {
  }
}
</script>

<style scoped lang="scss">
  @import "@/assets/styles/mixin.scss";

  .health-info {
    .health-con{
      position: absolute;
      bottom:pxToRem(140px);
      top:pxToRem(0px);
      z-index: 103;
      background: #fff;

      max-height:565px;
      overflow-y: scroll;
      left:0;
      .con{
        font-size: pxToRem(30px);
        color: #333333;
        line-height: pxToRem(48px);
        padding: pxToRem(20px) pxToRem(30px) 0;
        .tips{
          span{
            color: $color-active-text;
          }
        }
        .info{
          padding-left:pxToRem(30px);
        }

      }
      .helth-btns{
        position: fixed;
        bottom:0;
        height:pxToRem(140px);
        line-height:pxToRem(140px) ;
        z-index: 102;
        display: flex;
        justify-content: center;
        width:100%;
        .btn{
          margin:0 pxToRem(15px);
          margin-top:pxToRem(30px);
          font-size: pxToRem(30px);
          padding:0 pxToRem(45px);
          height:pxToRem(80px);
          line-height:pxToRem(80px);
          border-radius :pxToRem(40px);
          &.btn-cancel{
            color: #A5A8A8;
            border: 1px solid #D5D8D8;
          }
          &.btn-sure{
            background: #1CD7D1;
            color: #FFFFFF;
          }
        }
      }
    }

  }
</style>
